.promo__title {
  font-weight: 700;
  font-size: 27px;
  line-height: 30px;
  margin: 0 auto;
  width: 250px;
  text-align: center;
  padding: 0 0 119px 0;
}

.promo__group {
  margin: 0 0 33px 0;
}

.promo__back-light {
  background: $base-green-l;
}

.promo__back-hard {
  background: $base-green-d;
}

.promo__link {
  display: block;
  font-weight: 700;
  font-size: 19px;
  line-height: 24px;
  color: #ffffff;
  padding: 25px 0 30px 30px;
  position: relative;

  &:hover,
  &:focus {
    transform: translateY(-5px);
  }

  &--light {
    background: $base-green-l;

    &::before {
      content: "";
      position: absolute;
      width: 40px;
      height: 62px;
      top: 23px;
      right: 36px;
      background: url("../../img/icons/interior.svg") no-repeat;
    }
  }

  &--dark {
    background: $base-green-d;

    &::before {
      content: "";
      position: absolute;
      width: 60px;
      height: 60px;
      top: 21px;
      right: 27px;
      background: url("../../img/icons/toy.svg") no-repeat;
    }
  }
}

@media (min-width: $tablet) {
  .promo {
    padding: 110px 0 0 0;
  }

  .promo__title {
    font-size: 43px;
    line-height: 50px;
    width: 500px;
    padding: 0 0 371px 0;
  }

  .promo__group {
    display: flex;
    position: relative;
    padding: 0 50px;
    margin: 0 0 42px 0;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      bottom: -1px;
      width: 100%;
      height: 24px;
      background: url("../../img/zigzag-tablet.svg");
      z-index: 5;
    }
  }

  .promo__back {
    width: 50%;
  }

  .promo__link {
    font-size: 20px;
    line-height: 30px;
    padding: 25px 0 45px 36px;
  }

  .promo__link--light::before {
    top: 30px;
  }

  .promo__link--dark::before {
    top: 32px;
    right: 34px;
  }
}
